<template>
  <div class="random">
    <input type="text" v-model="txtInput" id="txt" placeholder="Write your name...">
    <button id="btn" v-on:click="randomFunc(txtInput)">Submit</button><br>
    <h1 v-if="txtInput != null"> {{ this.message }}</h1>
    <img v-if="this.message != null" id="imgWubba" src="../assets/wubba.gif">
  </div>
</template>
<script>
export default {
  name: 'Random',
  data(){
    return{
      message: {}
    }
  },
  methods: {
    randomFunc(txt) {
      this.message = 'Hej! ' + txt + ', wubbalubba dub dub!!!!'
    }
  }
}
</script>
<style>
h1{
  color: white;
}
#imgWubba{
  width: 400px;
}
#txt{
  text-align: center;
  margin: 4em;
  color: white;
  background-color: #333333;
  border: none;
  border-radius: 6px;
  width: 145px;
  height: 24px;
}
#btn {
  margin: 4em;
  color: white;
  background-color: #333333;
  border: none;
  border-radius: 6px;
  width: 80px;
  height: 24px;
  cursor: pointer;
}
#btn:hover {
  background-color: #42b983;
}
</style>