<template>
  <div class="character">
    <div id="grid">
      <div id="charCard" v-for="prop in props" :key="prop.id">
        <img :src="prop.image">
        <p>{{ prop.name }}</p>
      </div>
    </div>
  </div>
</template>
<script>
const axios = require('axios')
export default {
  name: "Characters",
  data() {
    return {
      props: {
        name: {},
        image: {}
      }
    }
  },
  mounted () {
    axios
      .get('https://rickandmortyapi.com/api/character/')
      .then(response => (this.props = response.data.results))
      .catch(error => console.log(error))
  }
}
</script>
<style>
.character{
  width: 300px;
  height: auto;
  margin-left: auto;
  margin-right: auto;
}
p {
  color: white;
}
#charCard {
  margin-bottom: 4em;
}
#grid{
  padding-top: 4em;
  justify-content: center;
  display: grid;
  grid-column-gap: 4em;
  grid-template-columns: auto auto auto auto;
}
img {
  width: 150px;
  border-radius: 6px;
}
</style>