<template>
  <div class="location">
    <div id="grid">
      <div id="charCard" v-for="prop in props" :key="prop.id">
        <p>{{ prop.name }}</p>
        <p id="type">{{ prop.type }}</p>
        <div v-if="prop.type === 'Planet'">
          <img src="../assets/planet.png">
        </div>
        <div v-if="prop.type === 'Space station'">
          <img src="../assets/spacestation.png">
        </div>
        <div v-if="prop.type === 'Dream'">
          <img src="../assets/dream.jpg">
        </div>
        <div v-if="prop.type === 'Fantasy town'">
          <img src="../assets/fantasy.png">
        </div>
        <div v-if="prop.type === 'Resort'">
          <img src="../assets/resort.jpg">
        </div>
        <div v-if="prop.type === 'TV'">
          <img src="../assets/tv.jpg">
        </div>
        <div v-if="prop.type === 'Microverse'">
          <img src="../assets/micro.png">
        </div>
        <div v-if="prop.type === 'Cluster'">
          <img src="../assets/cluster.png">
        </div>
      </div>
    </div>
  </div>
</template>
<script>
const axios = require('axios')
export default {
  name: "Locations",
  data() {
    return {
      props: {
        name: {},
        type: {}
      }
    }
  },
  mounted () {
    axios
      .get('https://rickandmortyapi.com/api/location')
      .then(response => (this.props = response.data.results))
      .catch(error => console.log(error))
  }
}
</script>
<style>
  #type{
    color: #1ff189;
    opacity: 55%;
  }
</style>